      console.log(data);
      Object.keys(data).map(key=>{
          nav.push({
            name: key,
            url: '/'+key
          })
      })
      console.log(nav);
      that.nav = nav;
    });
  },
  computed: {
    name () {
      return this.$route.name
    },
    list () {
      return this.$route.matched.filter((route) => route.name || route.meta.label )
    }
  }
